import React, { useState, useRef } from 'react';
import styles from './style.module.sass';
import {NavLink} from 'react-router-dom';
import Link from '@models/Link';

interface Props {
    spaceBetween?: number,
    data: Array<Link>
}

const Filter : React.FC<Props> = ({spaceBetween,data}) =>{
    const [translate,setTranslate] = useState(0);
    const [prevTouch,setPrev] = useState([0,0])
    const wrapper = useRef(null as null | HTMLDivElement);
    const nav = useRef(null as null | HTMLElement);

    const handleTouchMove : React.TouchEventHandler<HTMLDivElement> = (e) => {
        const {clientX,clientY} = e.touches[0];
        if(prevTouch[0] !== 0){
            const nextStep = translate - prevTouch[0]+clientX;
            const scroll =  (nav.current?.scrollWidth as number) - (wrapper.current?.clientWidth as number);
            if(Math.abs(nextStep) > scroll || nextStep > 0) {
                if(nextStep>0 && translate !==0) {
                    setTranslate(0);
                } else if(Math.abs(nextStep) > scroll) {
                    setTranslate(-scroll);
                }
                return
            }
            setTranslate(nextStep);
        }
        setPrev([clientX,clientY]);
    }

    const handleTouchEnd : React.TouchEventHandler<HTMLDivElement> = () => {
        setPrev([0,0]);
    }

    return (
        <div ref={wrapper} onTouchMove={handleTouchMove} onTouchEnd={handleTouchEnd} className={styles.wrapper}>
            <nav ref={nav} style={{transform: `translateX(${translate}px)`}}>
                {(()=>data.map(({name,href})=> {
                    return href ? (
                    <NavLink style={{marginRight: spaceBetween || 20}} to="/shit" className={styles.element} activeClassName={styles.active}>
                        {name}
                    </NavLink>) : <button style={{marginRight: spaceBetween || 20}}  className={styles.element}>{name}</button>
                } ))()}
            </nav>
        </div>
    )
}

export default Filter;