import React from 'react';
import {NavLink} from 'react-router-dom';
import styles from './style.module.sass';

export interface Link {
    link : string;
    logo : string;
    name : string;
}

interface Props {
    links: Array<Link>
}

const MobileNav : React.FC<Props> = ({links}) => {
    return (
        <nav className={styles.wrapper}>
            {(()=>{
                return links.map(({link,logo,name},index)=> (
                    <NavLink className={styles.link} activeClassName={styles.active} exact={link === '/'}  to={link}>
                        <span>{name}</span>
                        <svg width={20} height={20}>
                            <use xlinkHref={logo} />
                        </svg>
                    </NavLink>
                ))
            })()}
        </nav>
    )
}

export default MobileNav;